//_____________ INPORTS
@import "_fonts";
@import "./materialize/materialize";
// @import ./bootstrap/bootstrap-grid

//_____________ FONTS
$regular: Roboto;
$rale: Raleway;
$awe: fontawesome;

//_____________ CONST
$accent: #e54911;
$cyan: #e6eff6;
$header: #3c4348;
$font: #363636;
$font-gray: #959595;
$font-dark: #707070;

//_____________ SETTINGS
html {
	font-size: 10px; }
body {
	min-width: 320px;
	position: relative;
	line-height: 1.75;
	overflow-x: hidden;
	font-size: 1rem;
	opacity: 1;
	font-family: $rale;
	font-weight: 400;
	overflow-scrolling: touch;
	margin: 0; }
a:hover, a:focus {
	text-decoration: none; }
a {
	color: inherit;
	text-decoration: none; }
input:hover, input:focus, a:focus, button:active, button:focus {
	outline: none; }


//_____________ B U T T O N S
.btn {
	@extend .btn;
	@extend .waves-light;
	@extend .cyan;
	background-color: $accent;
	&:hover {
		background-color: darken($accent,10); } }
